	export default {
		data() {
			return {
                style: {
                	pageHeight: 0,
                	contentViewHeight: 0
                },
				searchArr: [],
                hasSearch: false,
                searchFalg: false,
                initArr: [],
                tabNum: 0,
                itemFalg: true,
                unknow: '未知'
			}
		},
		methods: {
			numChanged(index){
                this.tabNum = index
            },
            tabChange(e){
                this.tabNum = e.detail.current
            },
            seachText(e){





            },
            toMovieItem(e){
                let movieId = e.currentTarget.id;
                uni.navigateTo({
                    url: '/pages/playPage/playPage?vodId='+movieId,
                    animationType: 'slide-in-right',
                    animationDuration: 300
                });
            }
		},
        computed: {
            scrollLeft() {
                return (this.tabNum - 1) * 60;
            }
        },
        onLoad() {
            const res = uni.getSystemInfoSync();
            this.style.pageHeight = res.windowHeight;
            this.style.contentViewHeight = res.windowHeight; //像素
            uni.request({
                url: this.websiteUrl + '/api/home/searchInit',
                method: 'GET',
                data: {},
                success: res => {
                    this.initArr = res.data;
                },
                fail: () => {},
                complete: () => {}
            });
        },
        onNavigationBarSearchInputConfirmed(e){
            uni.request({
                url: this.websiteUrl + '/api/home/search',
                method: 'GET',
                data: {
                    vodName: e.text
                },
                success: res => {
                    if (res.data.data != null) {
                        this.searchArr = res.data.data;
                        this.hasSearch = false;
                        this.searchFalg = true;
                        this.itemFalg = false;
                    } else{
                        this.hasSearch = true;
                        this.searchFalg = false;
                        this.itemFalg = false;
                    }
                    this.searchArr = res.data.data;
                },
                fail: () => {},
                complete: () => {}
            });
        },
        onNavigationBarButtonTap(){
            uni.navigateBack();
        },
        onNavigationBarSearchInputChanged(e){
            uni.request({
                url: this.websiteUrl + '/api/home/search',
                method: 'GET',
                data: {
                    vodName: e.text
                },
                success: res => {
                    if (res.data.data != null && e.text !== '') {
                        this.searchArr = res.data.data;
                        this.hasSearch = false;
                        this.searchFalg = true;
                        this.itemFalg = false;
                        return
                    } else if (res.data.data == null && e.text !== '') {
                        this.searchArr = [];
                        this.hasSearch = true;
                        this.searchFalg = false;
                        this.itemFalg = false;
                        return
                    } else {
                        this.searchArr = [];
                        this.hasSearch = false;
                        this.searchFalg = false;
                        this.itemFalg = true;
                        return
                    }
                },
                fail: () => {},
                complete: () => {}
            });
        }
	}
